<template>
    <div class="home">
        <div>
            <el-row :gutter="20">
                <el-col :span="9">
                    <CustomCard :title="``">
                        <chart-1
                            slot="content"
                            ref="device-static"
                            :height="100"
                        ></chart-1>
                        <el-dropdown trigger="click" @command="deviceStaticMenu" slot="cardMenu">
                            <span class="el-dropdown-link">
                                <img src="@/assets/d.svg">
                            </span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item command="ALL"></el-dropdown-item>
                                <el-dropdown-item command="ETH">ETH</el-dropdown-item>
                                <el-dropdown-item command="ETC">ETC</el-dropdown-item>
                                <el-dropdown-item command="BTC">BTC</el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </CustomCard>
                </el-col>
                <el-col :span="9">
                    <CustomCard :title="``">
                        <chart-1
                            slot="content"
                            ref="power-static"
                            :height="100"
                        ></chart-1>
                        <el-dropdown trigger="click" @command="powerStaticMenu" slot="cardMenu">
                            <span class="el-dropdown-link">
                                <img src="@/assets/d.svg">
                            </span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item command="ETH">ETH</el-dropdown-item>
                                <el-dropdown-item command="ETC">ETC</el-dropdown-item>
                                <el-dropdown-item command="BTC">BTC</el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </CustomCard>
                </el-col>
                <el-col :span="6">
                    <CustomCard :title="``">
                        <div style="height: 100px;" slot="content">
                            <div class="server-message">
                                <div class="left">
                                    WEB:
                                </div>
                                <div class="right">
                                    0.0.0.0:0000
                                </div>
                            </div>
                            <div class="server-message">
                                <div class="left">
                                    :
                                </div>
                                <div class="right">
                                    v-0.0.1(, )
                                </div>
                            </div>
                            <div class="server-message">
                                <div class="left">
                                    QQ: 
                                </div>
                                <div class="right">
                                    223312313
                                </div>
                            </div>
                            <div class="server-message">
                                <div class="left">
                                    Telegram:
                                </div>
                                <div class="right">
                                    xxxxxxx
                                </div>
                            </div>
                        </div>
                    </CustomCard>
                </el-col>
            </el-row>
        </div>
        <div>
            <el-row :gutter="20" class="floor-2">
                <el-col :span="24">
                    <port-list></port-list>
                </el-col>
            </el-row>
        </div>
        <div>
            <el-row :gutter="20" class="floor-3">
                <el-col :span="12">
                    <Log></Log>
                </el-col>
                <el-col :span="12">
                    <serve-message></serve-message>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
    import CustomCard from '@/components/customCard.vue'
    import PortList from '@/components/portList.vue'
    import Chart1 from '@/components/chart1.vue'
    import Log from '@/components/log.vue'
    import ServeMessage from '@/components/serveMessage.vue'
    import { mapState, mapActions, mapGetters } from 'vuex'

    export default {
        name: 'Home',
        components: {
            CustomCard,
            PortList,
            Chart1,
            Log,
            ServeMessage
        },
        data() {
            return {
            }
        },
        mounted() {
            this.updateTotalChart();
        //    this.$refs['device-static'].draw(this.testChartData);
        //    this.$refs['power-static'].draw(this.testChartData1);
        },
        computed: {
            ...mapGetters('port', {
                getServers: 'getServers'
            }),
            ...mapGetters('chart', {
                getChartData: 'getChartData'
            })
        },
        methods: {
            ...mapActions('chart', [
                'updateTotalChart'
            ]),
            updateOnlineChart(chartData, type) {
                let chartType = type;

                let chartOption = {
                    title: {
                        show: false
                    },
                    legend: {
                        show: true,
                        data: []
                    },
                    x: {
                        data: []
                    },
                    yAxis: [],
                    y: [{
                        name: '',
                        data: [],
                        _unit: ''
                    }]
                }

                if (!chartType || chartType == 'ALL') {
                    chartType = 'ALL';
                }
                
                if (chartType == 'ALL') {
                    chartOption.y[0].name = ''
                    chartOption.legend.data = ['']
                } else if (chartType == 'ETH') {
                    chartOption.y[0].name = 'ETH'
                    chartOption.legend.data = ['ETH']
                } else if (chartType == 'ETC') {
                    chartOption.y[0].name = 'ETC'
                    chartOption.legend.data = ['ETC']
                } else if (chartType == 'BTC') {
                    chartOption.y[0].name = 'BTC'
                    chartOption.legend.data = ['BTC']
                }
                

                for (let i = 0; i < chartData.length; i++) {
                    let targetData = chartData[i];
                    
                    chartOption.x.data.push(targetData[0]);
                    
                    if (chartType == 'ALL') {
                        chartOption.y[0].data.push(targetData[1].split(',')[0])
                    } else if (chartType == 'ETH') {
                        chartOption.y[0].data.push(targetData[1].split(',')[2])
                    } else if (chartType == 'ETC') {
                        chartOption.y[0].data.push(targetData[1].split(',')[1])
                    } else if (chartType == 'BTC') {
                        chartOption.y[0].data.push(targetData[1].split(',')[3])
                    }
                }

                this.$refs['device-static'].draw(chartOption);
            },
            updatePowerChart(chartData, type) {
                let chartType = type;

                let chartOption = {
                    colorIndex: 5,
                    title: {
                        show: false
                    },
                    legend: {
                        show: true,
                        data: []
                    },
                    x: {
                        data: []
                    },
                    yAxis: [],
                    y: [{
                        name: 'ETH',
                        data: [],
                        _unit: 'MB'
                    }]
                }

                if (!chartType || chartType == 'ETH') {
                    chartType = 'ETH';
                }

                
                if (chartType == 'ETH') {
                    chartOption.y[0].name = 'ETH'
                    chartOption.legend.data = ['ETH']
                } else if (chartType == 'ETC') {
                    chartOption.y[0].name = 'ETC'
                    chartOption.legend.data = ['ETC']
                } else if (chartType == 'BTC') {
                    chartOption.y[0].name = 'BTC'
                    chartOption.legend.data = ['BTC']
                }
                

                for (let i = 0; i < chartData.length; i++) {
                    let targetData = chartData[i];
                    
                    chartOption.x.data.push(targetData[0]);
                    
                    if (chartType == 'ETH') {
                        chartOption.y[0].data.push((targetData[2].split(',')[2] / 1000000).toFixed(2))
                    } else if (chartType == 'ETC') {
                        chartOption.y[0].data.push(targetData[2].split(',')[1])
                    } else if (chartType == 'BTC') {
                        chartOption.y[0].data.push(targetData[2].split(',')[3])
                    }
                }
                console.log(chartOption)
                this.$refs['power-static'].draw(chartOption);
            },
            deviceStaticMenu(type) {
                this.updateOnlineChart(this.getChartData, type);
            },
            powerStaticMenu(type) {
                this.updatePowerChart(this.getChartData, type);
            }
        },
        watch: {
            getChartData: function (val) {
                this.updateOnlineChart(val);
                this.updatePowerChart(val);
            }
        }
    }
</script>

<style lang="scss" scoped>
    .floor-1{
        padding: 20px 0 0 0;
    }
    .floor-2{
        padding: 20px 0;
    }

    .server-message{
        font-size: 14px;
        color: $fontColor3;
        display: flex;
        justify-content: space-between;
        margin-bottom: 10px;

        div{
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
        }
    }
</style>