<template>
    <div class="menu">
        <el-menu :default-active="$route.meta.number" class="el-menu-demo" mode="horizontal" @select="handleSelect">
            <el-menu-item index="1">
                <div class="menu-icon">
                    <i class="el-icon-menu"></i>
                    <div></div>
                </div>
            </el-menu-item>
            <el-menu-item index="2">
                <div class="menu-icon">
                    <i class="el-icon-position"></i>
                    <div></div>
                </div>
            </el-menu-item>
            <el-menu-item index="3">
                <div class="menu-icon">
                    <i class="el-icon-setting"></i>
                    <div></div>
                </div>
            </el-menu-item>
            <el-menu-item index="4">
                <div class="menu-icon">
                    <i class="el-icon-phone-outline"></i>
                    <div></div>
                </div>
            </el-menu-item>
        </el-menu>
    </div>
</template>

<script>

    export default {
        data() {
            return {
                menuActive: '1'
            }
        },
        components: {
        },
        mounted() {
        },
        methods: {
            handleSelect(key, keyPath) {
                switch (key) {
                    case '1':
                        this.$router.push({
                            path: '/',
                        });

                        break;
                    case '2':
                        this.$router.push({
                            path: '/port',
                        });

                        break;
                    case '3':
                        this.$router.push({
                            path: '/setting'
                        });

                        break;
                    case '4':
                        this.$router.push({
                            path: '/about'
                        });

                        break;
                    default:
                        break;
                }
            }
        },
        watch: {
        }
    }
</script>

