<template>
    <div>
        <CardTemplate>
            <div class="title">
                <div></div>
                <div>
                    <el-dropdown trigger="click" @command="handleCommand">
                        <span class="el-dropdown-link">
                            <img src="./../assets/d.svg">
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item icon="el-icon-plus" command="add-port"></el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
            </div>
            <template>
                <el-table 
                    :data="tableData" 
                    style="width: 100%"
                >
                    <el-table-column prop="status" label="#" width="30px">
                        <template slot-scope="scope">
                            <div v-if="scope.row.status" class="success"></div>
                            <div v-if="!scope.row.status" class="error"></div>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label=""
                        width="100px"
                        prop="port"
                        sortable
                    >
                        <template slot-scope="scope">
                            <el-button @click="intoPort(scope.row)" type="text">{{scope.row.port}}</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column prop="agreement" label="" width="100px" sortable>
                    </el-table-column>
                    <el-table-column prop="coin" label="" width="100px" sortable>
                    </el-table-column>
                    <el-table-column label="" width="120px" prop="power" sortable>
                        <template slot-scope="scope">
                            {{$Func.ethHashToPower(scope.row.power).power}}{{$Func.ethHashToPower(scope.row.power).unit}}
                        </template>
                    </el-table-column>
                    <el-table-column prop="device" label="" width="120px" sortable>
                    </el-table-column>
                    <el-table-column prop="commission" label="" width="120px" sortable>
                    </el-table-column>
                    <el-table-column prop="addressurl" label="">
                    </el-table-column>
                    <!-- <el-table-column prop="poolurl" label="">
                    </el-table-column> -->
                    <el-table-column prop="wallet" label="">
                    </el-table-column>
                    <el-table-column prop="remarks" label="">
                    </el-table-column>
                    <el-table-column
                        label=""
                    >
                        <template slot-scope="scope">
                            <el-button @click="intoPort(scope.row)" type="text" size="small"></el-button>
                            <el-button @click="editPort(scope.row)" type="text" size="small"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </template>
        </CardTemplate>
    </div>
</template>

<script>
    import { mapState, mapActions, mapGetters } from 'vuex'
    import CardTemplate from './cardTemplate.vue'

    export default {
        components: {
            CardTemplate
        },
        computed:{
            ...mapGetters('port', {
                getServers: 'getServers'
            }),
            // table
            tableData() {
                let res = [];
                let getServers = this.getServers;

                getServers.forEach(item => {
                    res.push({
                        port: Number(item.Addr.split(':')[1]),
                        agreement: item.IsTls ? 'SSL' : 'TCP',
                        coin: item.Type,
                        device: item.WorkerCount ? item.WorkerCount : 0,
                        commission: this.$Func.transPercent(item.PumpingRate, item.PumpingRateLimit) + '%',
                        status: (item.Status == 'running') ? 1 : 0,
                        wallet: item.Wallet ? item.Wallet : '',
                        remarks: item.Name ? item.Name : '-',
                        power: item.HashRate ? item.HashRate : 0,
                        addressurl: item.Addr
                    });
                })

                return res;
            }
        },
        mounted() {
            this.updateTotalServer();
        },
        methods: {
            ...mapActions('port', [
                'updateTotalServer'
            ]),
            handleCommand(command) {
                if (command == 'add-port') {
                    this.$modal.show('add-port', {
                        panelType: 'add'
                    });
                }
            },
            intoPort(row) {
                this.$router.push({
                    path: 'portDetails',
                    query: {
                        port: row.port
                    }
                });
            },
            editPort(row) {
                this.$modal.show('add-port', {
                    panelType: 'edit',
                    editPort: row.port
                });
            }
        },
        data() {
            return {
            }
        }
    }
</script>

<style scoped lang="scss">
    .success {
        background: #04ff04;
        width: 10px;
        height: 10px;
        border-radius: 50%;
    }

    .error {
        background: #fff;
        width: 10px;
        height: 10px;
        border-radius: 50%;
    }

    .title {
        font-size: 16px;
        color: $fontColor2;
        margin-bottom: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        img {
            cursor: pointer;
        }
    }
</style>