<template>
    <div>
        <card-template class="opt">
            <el-row :gutter="20">
                <el-col :span="4">
                    <el-button class="full" type="success" @click="addPort"></el-button>
                </el-col>
                <!-- <el-col :span="20" class="message">
                    <div>
                        : 3222
                    </div>
                    <div>
                        : 32910
                    </div>
                </el-col> -->
            </el-row>
        </card-template>
        <chart-1 :height="120" ref="port-chart" style="margin:20px 0;"></chart-1>
        <el-row>
            <el-col :span="24">
                <Port></Port>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    import {
        mapState,
        mapActions,
        mapGetters
    } from 'vuex'
    import cardTemplate from '@/components/cardTemplate.vue'
    import Port from '@/components/portList.vue'
    import Chart1 from '@/components/chart1.vue'

    export default {
        data() {
            return {
            }
        },
        computed: {
            ...mapGetters('port', {
                getServers: 'getServers'
            }),
            ...mapGetters('chart', {
                getChartData: 'getChartData'
            })
        },
        mounted() {
            this.updateTotalChart();
        },
        components: {
            Port,
            cardTemplate,
            Chart1
        },
        methods: {
            ...mapActions('chart', [
                'updateTotalChart'
            ]),
            addPort() {
                this.$modal.show('add-port', {
                    panelType: 'add'
                });
            },
            setPortChart(chartData) {
                if (!chartData.length) {
                    return;
                }
                
                let chartOption = {
                    title: {
                        show: false
                    },
                    legend: {
                        show: true,
                        data: []
                    },
                    x: {
                        data: []
                    },
                    yAxis: [],
                    y: []
                }

                let legendArray = [];
                let xArray = [];
                let yArray = [];
                
                for (let i = 0; i < chartData.length; i++) {
                    let target = chartData[i];
                    let portData = target[3].split(',');

                    xArray.push(target[0]);

                    // , p1, 
                    for (let p = 1; p < portData.length; p++) {
                        let targetPort = portData[p];
                        let splitPortData = targetPort.split(':'); 

                        // legend, 
                        // 
                        // bug
                        if (legendArray.indexOf(splitPortData[0]) == -1) {
                            let yObject = {
                                name: splitPortData[0],
                                data: [],
                                _unit: ''
                            };

                            legendArray.push(splitPortData[0]);

                            // , data
                            for (let n = 0; n < i; n++) {
                                yObject.data.push(0);
                            }
                            yObject.data.push(splitPortData[1]);
                            yArray.push(yObject);
                        } else {
                            for (let x = 0; x < yArray.length; x++) {
                                if (yArray[x].name == splitPortData[0]) {
                                    yArray[x].data.push(splitPortData[1]);
                                }
                            }
                        }
                    }
                }
                chartOption.legend.data = legendArray;
                chartOption.x.data = xArray;
                chartOption.y = yArray;

                console.log(chartOption)
                this.$refs['port-chart'].draw(chartOption);
            },
        },
        watch: {
            getChartData: function (val) {
                this.setPortChart(val);
            }
        }
    }
</script>

<style scoped lang="scss">
    .opt {
        margin-bottom: 20px;
    }

    .full {
        width: 100%;
    }

    .message {
        color: $fontColor3;
        font-size: 12px;
        text-align: right;
        line-height: 21px;
    }
</style>