<template>
    <div class="add-port" v-loading="portLoading">
        <div class="title" v-if="panelType == 'add'">
            
        </div>
        <div class="title" v-if="panelType == 'edit'">
            
        </div>
        <div class="form">
            <el-form :model="addPortData" label-width="100px" :label-position="`top`" ref="portForm">
                <el-row :gutter="20">
                    <el-col :span="7">
                        <el-form-item 
                            label="" 
                            prop="port" 
                            :rules="[
                                { required: true, message: ''},
                                { type: 'number', message: ''}
                            ]"
                        >
                            <el-input :disabled="panelType == 'edit'" placeholder="" v-model.number="addPortData.port" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="7">
                        <el-form-item 
                            label="" 
                            prop="coin" 
                            :rules="[
                                { required: true, message: ''},
                            ]"
                        >
                            <el-select v-model="addPortData.coin" placeholder="" @change="changeCoin">
                                <el-option label="ETH" value="ETH"></el-option>
                                <el-option label="BTC" value="BTC"></el-option>
                                <el-option label="ETC" value="ETC"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item 
                            label="" 
                        >
                            <el-input placeholder="0, " v-model.number="addPortData.maxConnect" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="4">
                        <el-form-item 
                            label="SSL" 
                        >
                            <el-switch :disabled="panelType == 'edit'" v-model="addPortData.ssl"></el-switch>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row :gutter="20">
                    <el-col v-if="!addPortData.customPoolURL" :span="20">
                        <el-form-item 
                            label="" 
                            prop="poolURL" 
                            :rules="[
                                { required: true, message: ''},
                            ]"
                        >  
                            <el-select v-model="addPortData.poolURL" placeholder="" style="width:100%;"> 
                                <el-option-group
                                    v-for="pool in addPortData.pools"
                                    :key="pool.label"
                                    :label="pool.label"
                                >
                                    <el-option
                                        v-for="item in pool.options"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-option-group>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col v-if="addPortData.customPoolURL" :span="20">
                        <el-form-item 
                            label="" 
                            prop="poolURL" 
                            :rules="[
                                { required: true, message: ''}
                            ]"
                        >
                            <el-input placeholder="" v-model="addPortData.poolURL" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="4">
                        <el-form-item 
                            label="" 
                        >
                            <el-switch v-model="addPortData.customPoolURL"></el-switch>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row :gutter="20">
                    <el-col v-if="!addPortData.customSparePoolURL" :span="20">
                        <el-form-item 
                            label="" 
                        >  
                            <el-select v-model="addPortData.sparePoolURL" placeholder="" style="width:100%;"> 
                                <el-option-group
                                    v-for="pool in addPortData.pools"
                                    :key="pool.label"
                                    :label="pool.label"
                                >
                                    <el-option
                                        v-for="item in pool.options"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-option-group>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col v-if="addPortData.customSparePoolURL" :span="20">
                        <el-form-item 
                            label="" 
                            prop="sparePoolURL" 
                        >
                            <el-input placeholder="" v-model="addPortData.sparePoolURL" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="4">
                        <el-form-item 
                            label="" 
                        >
                            <el-switch v-model="addPortData.customSparePoolURL"></el-switch>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div class="hr" style="margin-bottom:0;"></div>
            <el-form :model="addPortData" label-width="100px" :label-position="`left`">
                <el-collapse v-model="commissionPanel" @change="commissionHandle">
                    <el-collapse-item name="1">
                        <template slot="title">
                            <el-row :gutter="20">
                                <el-col :span="4">
                                    <el-form-item 
                                        label="" 
                                    >
                                            <el-switch v-model="addPortData.isCommission"></el-switch>
                                    </el-form-item>
                                </el-col>
                            </el-row>
                        </template>
                        <!-- <div class="hr"></div> -->
                        <el-row>
                            <el-col :span="24">
                                <el-form-item 
                                    label="" 
                                >
                                    <el-slider
                                        :step="0.1"
                                        :min="0"
                                        :max="100"
                                        v-model="addPortData.commission"
                                        show-input
                                        :format-tooltip="formatCommission"
                                    >
                                    </el-slider>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row :gutter="20">
                            <el-col :span="24">
                                <el-form-item 
                                    label="" 
                                >
                                    <el-input placeholder="" v-model="addPortData.commissionWallet" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row :gutter="20">
                            <el-col :span="24">
                                <el-form-item 
                                    label=""
                                >
                                    <el-input placeholder="" v-model="addPortData.commissionPool" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row :gutter="20">
                            <el-col :span="20">
                                <el-form-item 
                                    label="" 
                                >
                                    <el-input :disabled="!addPortData.openUnified" placeholder="" v-model="addPortData.unified" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="4">
                                <el-form-item class="right-radio">
                                    <el-tooltip class="item" effect="dark" content="" placement="top">
                                        <el-switch v-model="addPortData.openUnified"></el-switch>
                                    </el-tooltip>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row :gutter="20">
                            <el-col :span="12">
                                <el-form-item 
                                    label="" 
                                >
                                    <el-tooltip class="item" effect="dark" content="" placement="top">
                                        <el-switch :disabled="!addPortData.openUnified" v-model="addPortData.poolMode"></el-switch>
                                    </el-tooltip>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item 
                                    label="" 
                                >
                                    <el-tooltip class="item" effect="dark" content="" placement="top">
                                        <el-switch :disabled="!addPortData.openUnified" v-model="addPortData.intercept"></el-switch>
                                    </el-tooltip>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-collapse-item>
                </el-collapse>
                <div class="hr"></div>
                <el-row>
                    <el-col :span="24">
                        <el-form-item label="">
                            <el-input type="textarea" v-model="addPortData.desc"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <el-form label-width="100px" :label-position="`left`">
                <el-row>
                    <el-col :span="24">
                        <el-form-item>
                            <el-button v-if="panelType == 'add'" size="small" type="primary" @click="submitCreate"></el-button>
                            <el-button v-if="panelType == 'edit'" size="small" type="primary" @click="submitEdit"></el-button>
                            <el-button size="small" @click="onCancel"></el-button>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
        </div>
    </div>
</template>

<script>
    import { mapState, mapActions, mapGetters } from 'vuex'

    export default {
        computed:{
            // ...mapGetters('port', {
            //     getPortType: 'getPortType',
            //     getOnlay: 'getOnlay'
            // })
        },
        mounted() {
            this.updatePools();
        },
        data() {
            return {
                commissionPanel: [],
                portLoading: true,
                panelType: 'add',
                addPortData: {
                    port: '',   // 
                    coin: 'ETH',   // 
                    ssl: true,    // ssl
                    maxConnect: '',     // 
                    poolURL: '',        // 
                    ProxyIsTls: false,  // tls
                    sparePoolURL: '',   // 
                    StandbyProxyIsTls: false,   // tls
                    customPoolURL: false,       // 
                    customSparePoolURL: false,  // 
                    isCommission: false,    // 
                    commission: 0,          // 
                    commissionWallet: '',   // 
                    commissionPool: '',     // 
                    PumpingProxyIsTls: false,   // tls
                    openUnified: false,     // ,
                    unified: '',            // 
                    poolMode: false,         // 
                    desc: '',   // 
                    intercept: false,   // 
                    pools: []
                }
            }
        },
        methods: {
            ...mapActions('port', [
                'updateTotalServer'
            ]),
            commissionHandle() {
                if (this.addPortData.isCommission) {
                    this.commissionPanel = ['1'];
                } else {
                    this.commissionPanel = [];
                }
            },
            // , 
            updatePools() {
                if (this.addPortData.coin && this.$PRESET_POOL[this.addPortData.coin]) {
                    this.addPortData.pools = JSON.parse(JSON.stringify(this.$PRESET_POOL[this.addPortData.coin]));
                    this.addPortData.poolURL = '';
                } else {
                    this.addPortData.pools = [];
                    this.addPortData.poolURL = '';
                }
            },
            changeCoin() {
                this.updatePools();
            },
            submitCreate() {
                let addPortData = this.addPortData;
                let ref = this.$refs['portForm'];
                
                ref.validate(valid => {
                    if (valid) {
                        this.axios({
                            method: 'post',
                            url: `${this.$API.createPort}`,
                            data: {
                                ProxyAddr: addPortData.poolURL,
                                LocalAddr: `0.0.0.0:${addPortData.port}`,
                                localIsTls: addPortData.ssl,
                                ProxyIsTls: addPortData.ProxyIsTls,
                                Type: addPortData.coin,
                                PumpingRate: this.$Func.transCommission(addPortData.commission).effect,
                                PumpingRateLimit: this.$Func.transCommission(addPortData.commission).totalEffect,
                                Wallet: addPortData.commissionWallet,
                                UnifiedWallet: addPortData.unified,
                                StandbyProxyAddr: addPortData.sparePoolURL,
                                PumpingProxyAddr: addPortData.commissionPool,
                                StandbyProxyIsTls: addPortData.StandbyProxyIsTls,
                                PumpingProxyIsTls: addPortData.PumpingProxyIsTls,
                                IsPumping: addPortData.isCommission,
                                MaxConnect: addPortData.maxConnect ? addPortData.maxConnect : 0,
                                Name: addPortData.desc,
                                IsMiningPoolMode: addPortData.poolMode,
                                IsUnifiedWallet: addPortData.openUnified,
                                IsCustomProxyAddr: addPortData.customPoolURL,
                                IsCustomStandbyAddr: addPortData.customSparePoolURL
                            }
                        }).then(res => {
                            if (res.status == 200) {
                                this.$message.success('');

                                this.updateTotalServer();
                                this.onCancel();
                            } else {
                                this.$message.warning(`, : ${res.status}`);
                            }
                        }).catch(err => {
                            this.$message.error(err);
                        })
                    } else {
                        return false;
                    }
                });
            },
            submitEdit() {
                let addPortData = this.addPortData;
                
                this.axios({
                    method: 'put',
                    url: `${this.$API.createPort}/${addPortData.port}`,
                    data: {
                        ProxyAddr: addPortData.poolURL,
                        ProxyIsTls: addPortData.ProxyIsTls,
                        Type: addPortData.coin,
                        PumpingRate: this.$Func.transCommission(addPortData.commission).effect,
                        PumpingRateLimit: this.$Func.transCommission(addPortData.commission).totalEffect,
                        Wallet: addPortData.commissionWallet,
                        UnifiedWallet: addPortData.unified,
                        StandbyProxyAddr: addPortData.sparePoolURL,
                        PumpingProxyAddr: addPortData.commissionPool,
                        StandbyProxyIsTls: addPortData.StandbyProxyIsTls,
                        PumpingProxyIsTls: addPortData.PumpingProxyIsTls,
                        IsPumping: addPortData.isCommission,
                        MaxConnect: addPortData.maxConnect ? addPortData.maxConnect : 0,
                        Name: addPortData.desc,
                        IsMiningPoolMode: addPortData.poolMode,
                        IsUnifiedWallet: addPortData.openUnified,
                        IsCustomProxyAddr: addPortData.customPoolURL,
                        IsCustomStandbyAddr: addPortData.customSparePoolURL
                    }
                }).then(res => {
                    if (res.status == 200) {
                        this.$message.success('');

                        this.updateTotalServer();
                        this.onCancel();
                    } else {
                        this.$message.warning(`, : ${res.status}`);
                    }
                }).catch(err => {
                    this.$message.error(err);
                })
            },
            clean() {
                // this.addPortData.port = '';
                // this.addPortData.coin = '';
                // this.addPortData.ssl = '';
                // this.addPortData.maxConnect = '';
                // this.addPortData.poolURL = '';
                // this.addPortData.sparePoolURL = '';
                // this.addPortData.customPoolURL = false;
                // this.addPortData.customSparePoolURL = false;
                // this.addPortData.commission = 0;
                // this.addPortData.commissionWallet = '';
                // this.addPortData.commissionPool = '';
                // this.addPortData.openUnified = false;
                // this.addPortData.unified = '';
                // this.addPortData.poolMode = false;
            },
            requestEditData(port) {
                let requestURL = this.$API.getPort + '/' + port;

                this.axios.get(requestURL).then(res => {
                    if (res.status == 200) {
                        this.setEditData(res.data.Server)
                    } else {
                        this.$message.error( `ERROR: ${res.error}`);   
                    }

                    this.portLoading = false;
                }).catch(err => {
                    this.$message.error(err);
                    console.error(err);

                    this.portLoading = false;
                })
            },
            setEditData(data) {
                this.addPortData.port = Number(data.LocalAddr.split(':')[1]);
                this.addPortData.coin = data.Type;
                this.changeCoin();
                this.addPortData.commission = this.$Func.transPercent(data.PumpingRate, data.PumpingRateLimit),
                this.addPortData.maxConnect = data.MaxConnect ? data.MaxConnect : '';
                this.addPortData.ssl = data.LocalIsTls;
                this.addPortData.poolURL = data.ProxyAddr;
                this.addPortData.sparePoolURL = data.StandbyProxyAddr;
                this.addPortData.commissionWallet = data.Wallet;
                this.addPortData.commissionPool = data.PumpingProxyAddr;
                this.addPortData.isCommission = data.IsPumping;
                this.commissionHandle();
                this.addPortData.desc = data.Name;
                this.addPortData.unified = data.UnifiedWallet;
                this.addPortData.openUnified = data.IsUnifiedWallet;
                this.addPortData.customPoolURL = data.IsCustomProxyAddr;
                this.addPortData.customSparePoolURL = data.IsCustomStandbyAddr;
                this.addPortData.poolMode = data.IsMiningPoolMode;
            },
            setPanelType(type) {
                this.panelType = type;

                if (type == 'add') {
                    this.portLoading = false;
                }
            },
            formatCommission(val) {
                return `${val}%`
            },
            onCancel() {
                this.$modal.hide('add-port')
            }
        },
        watch: {
            'addPortData.openUnified' (val) {
                if (!val) {
                    this.addPortData.unified = '';
                    this.addPortData.poolMode = false;
                    this.addPortData.intercept = false;
                }
            }
        }
    }
</script>

<style lang="scss">
    .add-port {
        padding: 20px;

        .title {
            color: $fontColor2;
            margin-bottom: 20px;
        }

        .hr{
            background: #3a4250;
            width: 100%;
            height: 1px;
            margin-bottom: 20px;
        }

        .right-radio .el-form-item__content{
            margin-left: 0!important;
        }

        .el-loading-mask{
            background-color: rgba(0,0,0,.5);
        }

        .el-icon-question{
            font-size: 20px;
            display: inline-flex;
            align-items: center;
            vertical-align: middle;
        }
    }
</style>