<template>
    <div>
        <div class="box">
            <div class="left">
                <el-row :gutter="24">
                    <el-col :span="24">
                        <el-row :gutter="10" class="row">
                            <el-col :span="24">
                                <el-button class="full" type="primary" @click="editPort"></el-button>
                            </el-col>
                        </el-row>
                        <div class="msg-box">
                             <el-row class="msg-row" :gutter="10">
                                 <el-col :span="12">
                                    <div class="title">
                                        
                                    </div>
                                    <div class="msg">
                                        {{info.port}}
                                    </div>
                                </el-col>
                                <el-col :span="12">
                                    <div class="title">
                                        
                                    </div>
                                    <div class="msg">
                                        {{info.type}}
                                    </div>
                                </el-col>
                            </el-row>
                             <el-row class="msg-row" :gutter="10">
                                <el-col :span="12">
                                    <div class="title">
                                         / 
                                    </div>
                                    <div class="msg">
                                        {{info.online}}
                                    </div>
                                </el-col>
                                <el-col :span="12">
                                    <div class="title">
                                        
                                    </div>
                                    <div class="msg">
                                        {{info.connectCount}}
                                    </div>
                                </el-col>
                            </el-row>
                            <el-row class="msg-row" :gutter="10">
                                <el-col :span="12">
                                    <div class="title">
                                        SSL
                                    </div>
                                    <div class="msg">
                                        <span v-if="info.ssl"></span>
                                        <span v-if="!info.ssl"></span>
                                    </div>
                                </el-col>
                                <el-col :span="12">
                                    <div class="title">
                                        
                                    </div>
                                    <div class="msg">
                                        {{info.PumpingRate}}%
                                    </div>
                                </el-col>
                            </el-row>
                            <el-row class="msg-row" :gutter="10">
                                <el-col :span="12">
                                    <div class="title">
                                        
                                    </div>
                                    <div class="msg">
                                        {{info.wallet}}
                                    </div>
                                </el-col>
                                <el-col :span="12">
                                    <div class="title">
                                        
                                    </div>
                                    <div class="msg">
                                        {{info.unified}}
                                    </div>
                                </el-col>
                            </el-row>
                            <el-row class="msg-row" :gutter="10">
                                
                            </el-row>
                            <el-row class="msg-row" :gutter="10">
                                <el-col :span="24">
                                    <div class="title">
                                        
                                    </div>
                                    <div class="msg">
                                        {{info.addr}}
                                    </div>
                                </el-col>
                            </el-row>
                            <el-row class="msg-row" :gutter="10">
                                <el-col :span="24">
                                    <div class="title">
                                        
                                    </div>
                                    <div class="msg">
                                        {{info.defaultPool}}
                                    </div>
                                </el-col>
                            </el-row>
                            <el-row class="msg-row" :gutter="10">
                                <el-col :span="24">
                                    <div class="title">
                                        
                                    </div>
                                    <div class="msg">
                                        {{info.standbyPool}}
                                    </div>
                                </el-col>
                            </el-row>
                            <el-row class="msg-row" :gutter="10">
                                <el-col :span="24">
                                    <div class="title">
                                        
                                    </div>
                                    <div class="msg">
                                        {{info.pumpingPool}}
                                    </div>
                                </el-col>
                            </el-row>
                        </div>                    
                        <el-row :gutter="10" class="row">
                            <el-col :span="24">
                                <el-button class="full" type="warning"></el-button>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="24">
                                <el-button class="full" type="danger"></el-button>
                            </el-col>
                        </el-row>
                    </el-col>
                </el-row>
            </div>
            <div class="right">       
                <div class="center-msg">
                    <div class="msg-item">
                        <div class="title">
                            {{total.power}}
                        </div>
                        <div class="ctext">
                            
                        </div>
                    </div>
                    <!-- <div class="msg-item">
                        <div class="title">
                            {{total.poolPower}}
                        </div>
                        <div class="ctext">
                            
                        </div>
                    </div> -->
                    <div class="msg-item">
                        <div class="title">
                            {{total.ping}}
                        </div>
                        <div class="ctext">
                            
                        </div>
                    </div>
                    <div class="msg-item">
                        <div class="title">
                            {{total.effect}}
                        </div>
                        <div class="ctext">
                            
                        </div>
                    </div>
                    <div class="msg-item">
                        <div class="title">
                            {{total.invalid}}
                        </div>
                        <div class="ctext">
                            
                        </div>
                    </div>
                </div>
                <div class="chart-1">
                    <chart-1 
                        :height="100"
                        ref="port-power"
                    ></chart-1>
                </div>
                <card-template>
                    <template>
                        <el-table :data="tableData" style="width: 100%">
                            <el-table-column prop="status" label="#" width="30px">
                                <template slot-scope="scope">
                                    <div v-if="scope.row.status" class="success"></div>
                                    <div v-if="!scope.row.status" class="error"></div>
                                </template>
                            </el-table-column>
                            <el-table-column prop="name" label="" sortable>
                                <template slot-scope="scope">
                                    <el-button @click="showDevice(scope.row)" type="text">{{scope.row.name}}</el-button>
                                </template>
                            </el-table-column>
                            <el-table-column prop="power" label="" sortable>
                            </el-table-column>
                            <!-- <el-table-column prop="poolPower" label="">
                            </el-table-column> -->
                            <el-table-column prop="effect" label="" sortable>
                                <template slot-scope="scope">
                                    {{formatEffect(scope.row.effect, scope.row.invalid)}}
                                </template>
                            </el-table-column>
                            <el-table-column prop="invalid" label="" sortable>
                                <template slot-scope="scope">
                                    {{formatInvalid(scope.row.effect, scope.row.invalid)}}
                                </template>
                            </el-table-column>
                            <el-table-column prop="ping" label="" sortable>
                            </el-table-column>
                            <el-table-column
                                label=""
                            >
                                <template slot-scope="scope">
                                    <el-button @click="showDevice(scope.row)" type="text" size="small"></el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    </template>
                </card-template>
            </div>
        </div>
    </div>
</template>

<script>
    import Chart1 from '@/components/chart1.vue'
    import CardTemplate from '@/components/cardTemplate.vue'

    export default {
        data() {
            return {
                port: 0,
                activeComponent: true,
                interval: null,
                info: {
                    port: '-',
                    type: '-',
                    online: '-/-',
                    connectCount: '-/',
                    ssl: false,
                    wallet: '-',
                    PumpingRate: 0,
                    addr: '-',
                    defaultPool: '-',
                    standbyPool: '-',
                    pumpingPool: '-',
                    unified: '-'
                },
                total: {
                    power: 0,
                    poolPower: 0,
                    ping: 0,
                    effect: 0,
                    invalid: 0
                },
                tableData: []
            }
        },
        computed: {
            portNumber() {
                return this.$route.query.port;
            }
        },
        destroyed() {
            this.activeComponent = false;

            window.clearInterval(this.interval);
        },
        mounted() {
            this.port = this.$route.query.port;

            this.activeComponent = true;

            this.getDeviceList();

            this.getPortPower();

            this.interval = setInterval(() => {
                if (this.activeComponent) {
                    this.getDeviceList();
                }
            }, 5000);
        },
        components: {
            Chart1,
            CardTemplate
        },
        methods: {
            getPortPower() {
                let requestURL = this.$API.getPortPower.replace('{port}', this.port);

                this.axios.get(requestURL).then(res => {
                    if (res.status == 200) {
                        console.log(res)
                        this.setChartData(res.data.data);
                    } else {
                        this.$message.error( `ERROR: ${res.error}`);   
                    }
                }).catch(err => {
                    this.$message.error(err);
                    console.error(err);
                })
            },
            setChartData(data) {
                let chartData = {
                    title: {
                        show: false
                    },
                    legend: {
                        show: true,
                        data: ['', '', '', ''],
                        selected: {
                            '': true,
                            '': false,
                            '': false,
                            '': false
                        }
                    },
                    x: {
                        data: []
                    },
                    yAxis: [{}, {}, {}],
                    y: [
                        {
                            name: '',
                            data: [],
                            _unit: 'MB',
                            yAxisIndex: 0
                        },
                        {
                            name: '',
                            data: [],
                            _unit: '',
                            yAxisIndex: 1
                        },
                        {
                            name: '',
                            data: [],
                            _unit: '',
                            yAxisIndex: 1
                        },
                        {
                            name: '',
                            data: [],
                            _unit: '',
                            yAxisIndex: 2
                        }
                    ]
                }

                let xArray = [];    // 
                let yArray = [];    // 
                let y2Array = [];   // 
                let y3Array = [];   // 
                let y4Array = [];   // 

                data.forEach((item) => {
                    if (item[0]) {
                        xArray.push(item[0]);
                        yArray.push((item[1] / 1000000).toFixed(2));
                        y2Array.push(item[2]);
                        y3Array.push(item[3]);
                        y4Array.push(item[4]);
                    }
                })

                chartData.x.data = xArray;
                chartData.y[0].data = yArray;
                chartData.y[1].data = y2Array;
                chartData.y[2].data = y3Array;
                chartData.y[3].data = y4Array;
                console.log(chartData)
                this.$refs['port-power'].draw(chartData);
            },
            // 
            formatEffect(effect, invalid) {
                let number = effect / (effect + invalid) * 100;
                let res = 0;
                if (!isNaN(number)) {
                    res = parseInt(number);
                }
                return `${effect} ${res}%`
            },
            // 
            formatInvalid(effect, invalid) {
                let number = invalid / (effect + invalid) * 100;
                let res = 0;
                if (!isNaN(number)) {
                    res = parseInt(number);
                }
                return `${invalid} ${res}%`
            },
            showDevice(row) {
                let id = row.id;

                this.$modal.show('deviceDetails', {
                    id,
                    port: this.info.port,
                    row 
                });
            },
            getDeviceList() {
                let port = this.$route.query.port;
                let api = this.$API.getPort + '/' + port;

                this.axios.get(api).then(res => {
                    if (res.status == 200) {
                        let infoData = res.data.Server;
                        let connects = res.data.Workers;
                        let keys = Object.keys(connects);
                        let tableData = [];
                        let totalPower = 0;
                        let totalPing = 0;
                        let totalEffect = 0;
                        let totalInvalid = 0;

                        // 
                        if (keys.length) {
                            keys.forEach(key => {
                                let targetConnect = connects[key];
                                let targetDevice = this.createDeviceByConnect(key, targetConnect);

                                tableData.push(targetDevice);
                                // 
                                totalPower += targetConnect.HashRate;
                                totalPing += parseFloat(targetConnect.Delays ? targetConnect.Delays : 0)
                                totalEffect += targetConnect.ValidShares;
                                totalInvalid += targetConnect.InvalidShares;
                            });
                        }

                        // 
                        let effectPercent = (totalEffect / (totalEffect + totalInvalid) * 100).toFixed(2);
                        let invalidPercent = (totalInvalid / (totalEffect + totalInvalid) * 100).toFixed(2);
                        isNaN(effectPercent) && (effectPercent = 0);
                        isNaN(invalidPercent) && (invalidPercent = 0);
                        this.total.power = this.$Func.ethHashToPower(totalPower).power + this.$Func.ethHashToPower(totalPower).unit;
                        this.total.ping = parseInt(totalPing / keys.length) + 'ms';
                        this.total.effect = `${totalEffect} ${parseInt(effectPercent)}%`;
                        this.total.invalid = `${totalInvalid} ${parseInt(invalidPercent)}%`;

                        // 
                        this.tableData = tableData;

                        // 
                        this.info.port = infoData.LocalAddr.split(':')[1];
                        this.info.type = infoData.Type;
                        this.info.ssl = infoData.LocalIsTls;
                        this.info.wallet = infoData.Wallet ? infoData.Wallet : '-';
                        this.info.unified = infoData.UnifiedWallet ? infoData.UnifiedWallet : '-';
                        this.info.addr = infoData.LocalAddr;
                        this.info.defaultPool = infoData.ProxyAddr;
                        this.info.standbyPool = infoData.StandbyProxyAddr ? infoData.StandbyProxyAddr : '-';
                        this.info.pumpingPool = infoData.PumpingProxyAddr ? infoData.PumpingProxyAddr : '-';
                        this.info.connectCount = (infoData.MaxConnect == 0) ? '' : infoData.MaxConnect;
                    } else {
                        this.$message.error( `ERROR: ${res.error}`);   
                    }
                }).catch(err => {
                    this.$message.error(err);
                    console.error(err)
                })
            },
            createDeviceByConnect(id, connect) {
                let status = connect.IsActive;

                return {
                    id: id,
                    name: connect.Name,
                    power: status ? this.$Func.ethHashToPower(connect.HashRate).power + this.$Func.ethHashToPower(connect.HashRate).unit : '0.00MB',
                    poolPower: status ? '-' :'-',
                    effect: status ? connect.ValidShares :'-',
                    invalid: status ? connect.InvalidShares :'-',
                    ping: status ? (parseInt((connect.Delays ? connect.Delays : 0)) + 'ms') :'-',
                    status
                }
            },
            editPort() {
                this.$modal.show('add-port', {
                    panelType: 'edit',
                    editPort: this.port
                });
            }
        }
    }
</script>

<style lang="scss" scoped>
    .box{
        display: flex;

        .left{
            flex: 0 0 300px;
            width: 300px;
        }

        .right{
            flex: 1;
            width: 0;
            margin-left: 20px;
        }
    }

    .full {
        width: 100%;
    }

    .row{
        margin-bottom: 10px;
    }

    .msg-row{
        padding: 10px 0;
        color: $fontColor1;
        font-size: 13px;
        word-break: break-all;

        .title{
            font-size: 14px;
            margin-bottom: 10px;
        }

        .msg{
            font-size: 15px;
            font-weight: bold;
            padding-left: 25px;
            text-align: left;
            color: $fontColor4;
        }
    }

    
    .msg-box{
        margin-bottom: 20px;
        margin-top: 20px;
    }

    .chart-1{
        width: 100%;
        margin-bottom: 20px;
    }

    .center-msg{
        display: flex;
        justify-content: space-between;
        margin: 0 0 20px 0;

        .msg-item{
            text-align: center;

            .title{
                font-size: 32px;
                font-weight: bold;
                color: $fontColor4;
            }

            .ctext{
                font-size: 15px;
                color: $fontColor1;
            }
        }
    }

    .table-content{
        display: flex;

        .table-left{
            flex: 1;
            width: 0;
        }

        .table-right{
            flex: 0 0 400px;
            width: 400px;
        }
    }

    .success {
        background: #04ff04;
        width: 10px;
        height: 10px;
        border-radius: 50%;
    }

    .error {
        background: #fff;
        width: 10px;
        height: 10px;
        border-radius: 50%;
    }
</style>