import axios from 'axios'
import CONFIG from '@/script/config.js'
import { Message } from 'element-ui'

const SERVER_PORT = CONFIG.API.SERVER_PORT;
const API = CONFIG.API;

const state = () => ({
    servers: []
})

const getters = {
    getServers: (state, getters, rootState) => {
        return state.servers
    }
}

const actions = {
    // 
    updateTotalServer ({ state, commit }) {
        return new Promise((resolve, reject) => {
            axios.get(API.getTotalConfig).then(res => {
                if (res.status == 200) {
                    commit('setTotalServer', res.data.Ports);

                    resolve(res);
                } else {
                    reject(res);
                }
            }).catch(err => {
                Message.error(err);
            });
        })
        // commit('setTotalServer')
    }
}

const mutations = {
    setTotalServer (state, data) {
        state.servers = data
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}