import axios from 'axios'
import CONFIG from '@/script/config.js'
import { Message } from 'element-ui'

const API = CONFIG.API;

const state = () => ({
    chartData: []
})

const getters = {
    getChartData: (state, getters, rootState) => {
        return state.chartData
    }
}

const actions = {
    // 
    updateTotalChart ({ state, commit }) {
        return new Promise((resolve, reject) => {
            axios.get(API.getChart).then(res => {
                if (res.status == 200) {
                    commit('setTotalChart', res.data.data);

                    resolve(res);
                } else {
                    reject(res);
                }
            }).catch(err => {
                Message.error(err);
            });
        })
        // commit('setTotalServer')
    }
}

const mutations = {
    setTotalChart (state, data) {
        state.chartData = data
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}