<template>
    <div :style="{height:`${height}px`}" v-loading="!lodadedChart">
        <div :style="{height:`${height}px`}" ref="chart1">
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                lodadedChart: false
            }
        },
        components: {},
        props: {
            height: {
                default: 120
            }
        },
        mounted() {
            let chartElement = this.$refs.chart1;
            this.echarts = this.$echarts.init(chartElement);

            // this.draw();

            window.addEventListener('resize', () => {
                this.echarts.resize();
            })
        },
        methods: {
            draw($chartData) {
                let echarts = this.echarts;
                let bgColor = "rgba(0, 0, 0, 0)";
                let color = [
                    "#0090FF",
                    "#36CE9E",
                    "#b2ff5d",
                    "#FF515A",
                    "#8B5CFF",
                    "#00CA69"
                ];


                const hexToRgba = (hex, opacity) => {
                    let rgbaColor = "";
                    let reg = /^#[\da-f]{6}$/i;
                    if (reg.test(hex)) {
                        rgbaColor =
                            `rgba(${parseInt("0x" + hex.slice(1, 3))},${parseInt("0x" + hex.slice(3, 5))},${parseInt("0x" + hex.slice(5, 7))},${opacity})`;
                    }
                    
                    return rgbaColor;
                }

                let option = {
                    title: {
                        show: $chartData.title.show,
                        text: $chartData.title.text,
                        right: 0,
                        textStyle: {
                            fontSize: 14,
                            color: '#919eab'
                        }
                    },
                    backgroundColor: 'rgba(0, 0, 0, 0)',
                    legend: {
                        show: $chartData.legend.show,
                        selected: $chartData.legend.selected ? $chartData.legend.selected : {},
                        top: 17,
                        right: 30,
                        icon: 'roundRect',
                        itemWidth: 10,
                        itemHeight: 8,
                        itemGap: 15,
                        textStyle: {
                            fontFamily: 'MicrosoftYaHei',
                            fontSize: 14,
                            color: 'rgba(255, 255, 255, 1)'
                        },
                        data: $chartData.legend.data
                    },
                    grid: {
                        top: '0',
                        left: '0',
                        right: '0',
                        bottom: '0',
                        containLabel: false,
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'line',
                            lineStyle: {
                                color: 'rgba(113, 113, 113, 1)',
                            },
                        },
                        backgroundColor: 'rgba(42, 51, 74, 1)',
                        borderColor: 'transparent',
                        formatter: function (params) {
                            let returnData = '<div style="padding: 5px 10px;">'
                            returnData +=
                                '<span style="font-family: MicrosoftYaHei; font-size: 14px; color: rgba(210, 221, 249, 1);">' +
                                params[0].axisValue + '</span><br/>'
                            for (let i = 0; i < params.length; i++) {
                                returnData +=
                                    '<span style="display:inline-block; width:10px; height:8px; margin-right:5px; border-radius:1px; background-color:' +
                                    color[$chartData.colorIndex || (i % color.length)] + '"></span>'
                                returnData +=
                                    '<span style="font-family: MicrosoftYaHei; font-size: 14px; color: ' +
                                    color[$chartData.colorIndex || (i % color.length)] + '">' + params[i].seriesName +
                                    '</span><span style="font-family: Verdana; font-size: 14px; color: ' +
                                    color[$chartData.colorIndex || (i % color.length)] + '">' + params[i].value +
                                    '</span><span style="display:inline-block; margin-left: 4px; line-height: 10px; font-family: MicrosoftYaHei; font-size: 12px; color: ' +
                                    color[$chartData.colorIndex || (i % color.length)] + '">' + $chartData.y[params[i].seriesIndex]._unit + '</span><br/>'
                            }
                            returnData += '</div>'
                            return returnData
                        }
                    },
                    xAxis: {
                        type: 'category',
                        show: false,
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: 'rgba(52, 51, 51, 1)',
                            },
                        },
                        splitLine: {
                            show: false,
                        },
                        axisLabel: {
                            fontFamily: 'MicrosoftYaHei',
                            fontSize: 12,
                            color: 'rgba(113, 113, 113, 1)',
                        },
                        axisTick: {
                            show: false,
                            alignWithLabel: true,
                        },
                        boundaryGap: false,
                        data: $chartData.x.data,
                    },
                    yAxis: [
                        {
                            type: 'value',
                            name: $chartData.unit,
                            show: false,
                            nameTextStyle: {
                                fontFamily: 'MicrosoftYaHei',
                                fontSize: 12,
                                color: 'rgba(113, 113, 113, 1)',
                            },
                            min: 0,
                            axisLine: {
                                show: false,
                            },
                            splitLine: {
                                show: true,
                                lineStyle: {
                                    color: 'rgba(52, 51, 51, 0.8)',
                                },
                            },
                            axisLabel: {
                                show: true,
                                margin: 20,
                                textStyle: {
                                    fontFamily: 'MicrosoftYaHei',
                                    fontSize: 12,
                                    color: 'rgba(113, 113, 113, 1)',
                                },
                            },
                            axisTick: {
                                show: false,
                            }
                        }
                    ],
                    series: []
                };

                // , yAxis
                $chartData.yAxis.forEach((item, index) => {
                    if (index) {
                        option.yAxis.push({
                            type: 'value',
                            name: $chartData.unit,
                            show: false,
                            nameTextStyle: {
                                fontFamily: 'MicrosoftYaHei',
                                fontSize: 12,
                                color: 'rgba(113, 113, 113, 1)',
                            },
                            min: 0,
                            axisLine: {
                                show: false,
                            },
                            splitLine: {
                                show: true,
                                lineStyle: {
                                    color: 'rgba(52, 51, 51, 0.8)',
                                },
                            },
                            axisLabel: {
                                show: true,
                                margin: 20,
                                textStyle: {
                                    fontFamily: 'MicrosoftYaHei',
                                    fontSize: 12,
                                    color: 'rgba(113, 113, 113, 1)',
                                },
                            },
                            axisTick: {
                                show: false,
                            }
                        })
                    }
                });

                $chartData.y.forEach((item, index) => {
                    let colorIndex = color[$chartData.colorIndex] || color[index % color.length];
                    let pushData = {
                        name: item.name,
                        type: 'line',
                        smooth: true,
                        showAllSymbol: true,
                        symbol: 'none',
                        symbolSize: 5,
                        lineStyle: {
                            color: hexToRgba(colorIndex, 1)
                        },
                        label: {
                            show: false
                        },
                        itemStyle: {
                            color: hexToRgba(colorIndex, 0.4),
                            borderColor: hexToRgba(colorIndex, 0.4),
                            borderWidth: 3
                        },
                        areaStyle: {
                            normal: {
                                color: new this.$echarts.graphic.LinearGradient(
                                    0,
                                    0,
                                    0,
                                    1,
                                    [{
                                            offset: 0,
                                            color: hexToRgba(colorIndex, 0.4),
                                        },
                                        {
                                            offset: 1,
                                            color: hexToRgba(colorIndex, 0),
                                        },
                                    ],
                                    false
                                ),
                                shadowColor: hexToRgba(colorIndex, 0.4),
                                shadowBlur: 20,
                            },
                        },
                        data: item.data
                    }
                    
                    // y, 
                    if ($chartData.yAxis.length) {
                        pushData.yAxisIndex = item.yAxisIndex || index;
                    }

                    option.series.push(pushData)
                });
                console.log(option)
                echarts.setOption(option);
                
                this.lodadedChart = true;
            }
        }
    }
</script>

<style scoped lang="scss">

</style>