<template>
    <div class="device-info">
        <div class="big-title">{{name}}</div>
        <chart-1 
            :height="100"
            ref="device-power"
        ></chart-1>
        <div class="container">
            <div class="center-msg">
                <div class="msg-item">
                    <div class="title">
                        {{power}}
                    </div>
                    <div class="ctext">
                        
                    </div>
                </div>
                <!-- <div class="msg-item">
                    <div class="title">
                        {{poolPower}}
                    </div>
                    <div class="ctext">
                        
                    </div>
                </div> -->
                <div class="msg-item">
                    <div class="title">
                        {{ping}}
                    </div>
                    <div class="ctext">
                        
                    </div>
                </div>
                <div class="msg-item">
                    <div class="title">
                        {{validShares}}
                    </div>
                    <div class="ctext">
                        
                    </div>
                </div>
                <div class="msg-item">
                    <div class="title">
                        {{invalidShares}}
                    </div>
                    <div class="ctext">
                        
                    </div>
                </div>
            </div>
            <div class="block">
                <el-timeline :reverse="reverse" v-loading="contentLoading">
                    <el-timeline-item v-for="(activity, index) in activities" :key="index"
                        :timestamp="activity.timestamp" :type="activity.type">
                        {{activity.content}}
                    </el-timeline-item>
                </el-timeline>
            </div>
        </div>
    </div>
</template>

<script>
    import Chart1 from '@/components/chart1.vue'

    export default {
        components: {
            Chart1
        },
        data() {
            return {
                reverse: true,
                activities: [],
                contentLoading: true,
                power: 0,
                poolPower: '-',
                ping: '-',
                validShares: '-',
                invalidShares: '-',
                name: '-'
            };
        },
        methods: {
            setId(id, port, row) {
                this.contentLoading = true;

                this.requestPower(id, port);
                this.requestInfo(id, port);
                this.setRow(row);
            },
            setRow(row) {
                let effect = row.effect / (row.effect + row.invalid) * 100;
                let invaild = row.invalid / (row.effect + row.invalid) * 100;

                this.power = row.power;
                this.ping = row.ping;
                this.validShares = `${row.effect} ${isNaN(effect) ? 0 : effect}%`;
                this.invalidShares = `${row.invalid} ${isNaN(invaild) ? 0 : effect}%`;
                this.name = row.name;
            },
            requestPower(id, port) {
                let requestURL = this.$API.getHistoryDevicePower.replace('{port}', port).replace('{worker_key}', id);

                this.axios.get(requestURL).then(res => {
                    if (res.status == 200) {
                        this.updatePower(res.data.data);
                    } else {
                        this.$message.error( `ERROR: ${res.error}`);   
                    }
                }).catch(err => {
                    this.$message.error(err);
                    console.error(err);
                })
            },
            requestInfo(id, port) {
                let requestURL = this.$API.getHistoryDeivceInfo.replace('{port}', port).replace('{worker_key}', id);

                this.axios.get(requestURL).then(res => {
                    if (res.status == 200) {
                        if (res.data.data[0]) {
                            this.updateInfo(res.data.data);
                            this.contentLoading = false;
                        } else {
                            this.$message.warning( ``);
                            this.contentLoading = true;
                        }
                    } else {
                        this.$message.error( `ERROR: ${res.error}`);
                    }
                }).catch(err => {
                    this.$message.error(err);
                    console.error(err);

                    this.contentLoading = false;
                })
            },
            updateInfo(data) {
                let res = [];

                data.forEach(item => {
                    if (item[0]) {
                        let ip = item[2];
                        let status = (item[0] == 1) ? '' : '';
                        let time = item[1];

                        if (item[0]) {
                            res.push({
                                content: `IP [${ip}] ${status}`,
                                timestamp: time,
                                type: (item[0] == 1) ? 'success' : 'danger'
                            })    
                        }
                    }
                });

                this.activities = res;

                return res;
            },
            updatePower(data) {
                let chartData = {
                    title: {
                        show: false
                    },
                    legend: {
                        show: true,
                        data: ['', '', ''],
                        selected: {
                            '': true,
                            '': false,
                            '': false
                        }
                    },
                    x: {
                        data: []
                    },
                    yAxis: [{}, {}],
                    y: [
                        {
                            name: '',
                            data: [],
                            _unit: 'MB'
                        },
                        {
                            name: '',
                            data: [],
                            _unit: '',
                            yAxisIndex: 1
                        },
                        {
                            name: '',
                            data: [],
                            _unit: '',
                            yAxisIndex: 1
                        }
                    ]
                }
                
                let xArray = [];
                let yArray = [];
                let y2Array = [];   // 
                let y3Array = [];   // 

                data.forEach(item => {
                    if (item[0]) {
                        xArray.push(item[0]);
                        yArray.push((item[2] / 1000000).toFixed(2));
                        y2Array.push(item[3]);
                        y3Array.push(item[4]);
                    }
                })

                chartData.x.data = xArray;
                chartData.y[0].data = yArray;
                chartData.y[1].data = y2Array;
                chartData.y[2].data = y3Array;

                if (data[0]) {
                    this.$refs['device-power'].draw(chartData);
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .device-info {
        .big-title {
            font-size: 23px;
            font-weight: bold;
            color: $fontColor4;
            padding: 20px 40px
        }

        .container {
            padding: 20px 40px;
        }

        .center-msg {
            display: flex;
            justify-content: space-between;
            margin: 0 0 20px 0;

            .msg-item {
                text-align: center;

                .title {
                    font-size: 24px;
                    font-weight: bold;
                    color: $fontColor4;
                }

                .ctext {
                    font-size: 12px;
                    color: $fontColor1;
                }
            }
        }

        .block{
            height: 400px;
            overflow-y: auto;
            padding-left: 2px;
            margin-top: 30px;

            .el-timeline{
                height: 400px;
            }
        }
    }
</style>