<template>
    <div class="custom-card-1">
        <CardTemplate>
            <div class="title" v-show="title">
                <div>{{title}}</div>
                <slot name="cardMenu"></slot>
            </div>
            <div class="custom-card-container">
                <slot name="content"></slot>
            </div>
        </CardTemplate>
    </div>
</template>

<script>
    import CardTemplate from './cardTemplate.vue'

    export default {
        props: ["title"],
        components: {
            CardTemplate
        },
        methods: {
            online() {
                return '75/80'
            },
            percent() {
                let percent = this.current / this.total * 100;

                if (percent) {
                    return percent;
                } else {
                    return 0;
                }
            }
        }
    }
</script>

