<template>
    <div id="app">
        <el-container>
            <el-header class="header-container">
                <Header></Header>
            </el-header>
            <el-main class="main-container">
                <div class="menu-container">
                    <Menu></Menu>
                </div>
                <div class="main-router">
                    <!-- <bread-crumb></bread-crumb> -->
                    <transition name="fade" mode="out-in">
                        <router-view />
                    </transition>
                </div>
            </el-main>
            <el-footer>
                <Footer></Footer>
            </el-footer>
        </el-container>
        <modal name="add-port"
             :width="`600px`" 
             :height="`auto`"
             @before-open="beforeOpenAddPort"
             @before-close="beforeCloseAddPort"
             @opened="openedAddPort"
             @closed="closedAddPort"
        >
            <add-port ref="addPort"></add-port>
        </modal>
        <modal name="deviceDetails"
            :width="'850px'"
            :height="'auto'"
            @before-open="beforeOpenDeviceDetails"
            @before-close="beforeCloseDeviceDetails"
            @opened="openedDeviceDetails"
            @closed="closedDeviceDetails"
        >
            <device-details ref="deviceDetails"></device-details>
        </modal>
    </div>
</template>

<script>
    // import BreadCrumb from '@/components/breadcrumb.vue'
    import Footer from './components/footer.vue'
    import Header from './components/header.vue'
    import Menu from './components/menu.vue'
    import AddPort from '@/components/addPort.vue'
    import DeviceDetails from '@/components/deviceDetails.vue'
    import { mapState, mapActions, mapGetters } from 'vuex'

    export default {
        data() {
            return {
                addPortModalParams: {
                    panelType: 'add'
                },
                editPortModalParams: {
                    editPort: 0
                },
                deviceDetailsParams: {
                    id: 0,
                    port: 0,
                    row: {}
                }
            }
        },
        components: {
            Header,
            Menu,
            Footer,
            // BreadCrumb,
            AddPort,
            DeviceDetails
        },
        mounted() {
            // this.$modal.show('deviceDetails')
            // this.updateTotalServer();
        },
        methods: {
            ...mapActions('port', [
                'updateTotalServer'
            ]),
            // 
            beforeOpenAddPort(event) {
                this.addPortModalParams.panelType = event.params.panelType;

                if (event.params.panelType == 'edit') {
                    this.editPortModalParams.editPort = event.params.editPort;
                }
            },
            beforeCloseAddPort(event) {},
            openedAddPort() {
                let ref = this.$refs.addPort;
                let panelType = this.addPortModalParams.panelType;
                let editPort = this.editPortModalParams.editPort;

                ref.setPanelType(panelType);

                if (panelType == 'edit') {
                    ref.requestEditData(editPort);
                }
                // ref.clean();
            },
            closedAddPort() {},
            // 
            beforeOpenDeviceDetails(event) {
                this.deviceDetailsParams.id = event.params.id;
                this.deviceDetailsParams.port = event.params.port;
                this.deviceDetailsParams.row = event.params.row;
            },
            beforeCloseDeviceDetails(event) {},
            openedDeviceDetails() {
                let ref = this.$refs.deviceDetails;

                ref.setId(this.deviceDetailsParams.id, this.deviceDetailsParams.port, this.deviceDetailsParams.row);
            },
            closedDeviceDetails() {}
        }
    }
</script>

<style lang="scss">
    body{
        margin: 0;
        padding: 0;
        background: $backgroundColor1;
        min-width: 1280px;
    }

    html{
        min-width: 1280px;
    }

    .main-router{
        background: $backgroundColor3;
        padding: $containerPadding1;
        margin-top: 80px;
        padding-bottom: 40px;
    }

    .el-header.header-container{
        padding: $containerPadding1;
        background: $backgroundColor1;
        position: fixed;
        width: 100%;
        z-index: 500;
    }

    .menu-container{
        padding: $containerPadding1;
        background: $backgroundColor2;
        position: fixed;
        width: 100%;
        z-index: 500;
    }

    .el-main.main-container{
        background: $backgroundColor3;
        padding: 0;
        margin-top: 60px;
    }

    .fade-enter-active, .fade-leave-active {
        transition: opacity .3s;
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
        opacity: 0;
    }
</style>