import { create, all } from 'mathjs'

const math = create(all, {number: 'BigNumber'})

// , math.js
const correctAccuracy = (expression) => {
    return math.format(math.evaluate(expression));
}

// 
const ethHashToPower = (hash) => {
    let res = {
        power: 0,
        unit: 'MB'
    }
    let base = 1000000;

    let MB = hash / base;

    if (MB >= 1000) {
        res.power = (hash / (base * 1000)).toFixed(2);
        res.unit = 'GB'
    } else {
        res.power = MB.toFixed(2);
        res.unit = 'MB';
    }


    return res;
}

// , 
const transCommission = (commission) => {
    let res = {
        effect: commission,
        totalEffect: 100
    }
    let decimals = (commission != Math.floor(commission)) ? (commission.toString()).split('.')[1].length : 0;
    let base = '1';

    if (decimals) {
        for (let i = 0; i < decimals; i++) {
            base += '0';
        }

        base = Number(base);
        res.effect = Number(correctAccuracy(`${commission} * ${base}`));
        res.totalEffect = 100 * base;
    }
    
    return res;
}

// 
const transPercent = (effect, totalEffect) => {
    return Number(correctAccuracy(`${effect} / ${totalEffect} * 100`));
}

export default {
    ethHashToPower,
    transCommission,
    transPercent,
    correctAccuracy
}